'use client'

import { useState, useEffect } from 'react'
import { 
  Activity,
  TrendingUp,
  Calendar,
  Zap,
  AlertCircle,
  ArrowUpRight,
  RefreshCw
} from 'lucide-react'

interface UsageSummary {
  plan: {
    name: string
    dailyLimit: number
    monthlyLimit: number
  }
  today: {
    used: number
    remaining: number
  }
  month: {
    used: number
    remaining: number
  }
  account?: {
    id: string
    email: string
    status: string
  }
  topEndpoints?: Array<{
    endpointId: string
    name: string
    calls: number
  }>
}

export default function UsagePage() {
  const [usage, setUsage] = useState<UsageSummary | null>(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [refreshing, setRefreshing] = useState(false)

  const loadUsage = async (isRefresh = false) => {
    try {
      if (isRefresh) setRefreshing(true)
      else setLoading(true)
      setError(null)

      const apiKey = localStorage.getItem('api_key')
      if (!apiKey) {
        throw new Error('API key not found')
      }

      // Try new endpoint first, fall back to legacy
      let response = await fetch('/api/v1/usage/summary', {
        headers: { 'x-api-key': apiKey },
      })
      
      if (!response.ok) {
        // Fallback to legacy endpoint
        response = await fetch('/api/v1/me/usage-summary', {
          headers: { 'x-api-key': apiKey },
        })
      }

      if (!response.ok) {
        const data = await response.json()
        throw new Error(data.error || 'Failed to load usage data')
      }

      const data = await response.json()
      setUsage(data)
    } catch (err: any) {
      setError(err.message || 'Failed to load usage data')
    } finally {
      setLoading(false)
      setRefreshing(false)
    }
  }

  useEffect(() => {
    loadUsage()
    // Auto-refresh every 30 seconds
    const interval = setInterval(() => loadUsage(true), 30000)
    return () => clearInterval(interval)
  }, [])

  if (loading) {
    return (
      <div className="flex items-center justify-center py-12">
        <div className="text-center">
          <div className="inline-block animate-spin rounded-full h-8 w-8 border-b-2 border-lime-400"></div>
          <p className="mt-4 text-gray-400">Loading usage data...</p>
        </div>
      </div>
    )
  }

  if (error) {
    return (
      <div className="p-6 bg-red-900/20 border border-red-800 rounded-lg">
        <div className="flex items-center gap-3">
          <AlertCircle className="w-5 h-5 text-red-400" />
          <span className="text-red-400">{error}</span>
        </div>
        <button
          onClick={() => loadUsage()}
          className="mt-4 px-4 py-2 bg-red-600 hover:bg-red-700 text-white rounded-lg transition-colors"
        >
          Retry
        </button>
      </div>
    )
  }

  if (!usage) {
    return null
  }

  const dailyPercentage = (usage.today.used / usage.plan.dailyLimit) * 100
  const monthlyPercentage = (usage.month.used / usage.plan.monthlyLimit) * 100

  const getPlanBadgeColor = (plan: string) => {
    switch (plan.toLowerCase()) {
      case 'free':
        return 'bg-gray-700 text-gray-300'
      case 'starter':
        return 'bg-blue-600 text-white'
      case 'pro':
        return 'bg-purple-600 text-white'
      case 'enterprise':
        return 'bg-amber-600 text-white'
      default:
        return 'bg-gray-700 text-gray-300'
    }
  }

  const getProgressColor = (percentage: number) => {
    if (percentage >= 90) return 'bg-red-500'
    if (percentage >= 70) return 'bg-amber-500'
    return 'bg-lime-400'
  }

  return (
    <div>
      <div className="flex items-center justify-between mb-8">
        <div>
          <h1 className="text-3xl font-bold">Usage</h1>
          <p className="text-gray-400 mt-1">Monitor your API usage and quota</p>
        </div>
        <button
          onClick={() => loadUsage(true)}
          disabled={refreshing}
          className="flex items-center gap-2 px-4 py-2 bg-gray-800 hover:bg-gray-700 text-white rounded-lg transition-colors disabled:opacity-50"
        >
          <RefreshCw className={`w-4 h-4 ${refreshing ? 'animate-spin' : ''}`} />
          Refresh
        </button>
      </div>

      {/* Plan Info Card */}
      <div className="mb-8 p-6 bg-gradient-to-r from-gray-900 via-gray-900 to-gray-800 rounded-xl border border-gray-800">
        <div className="flex items-center justify-between">
          <div className="flex items-center gap-4">
            <div className="w-12 h-12 rounded-xl bg-lime-400/10 flex items-center justify-center">
              <Zap className="w-6 h-6 text-lime-400" />
            </div>
            <div>
              <div className="flex items-center gap-3">
                <h2 className="text-xl font-semibold text-white">Current Plan</h2>
                <span className={`px-3 py-1 text-sm font-medium rounded-full capitalize ${getPlanBadgeColor(usage.plan.name)}`}>
                  {usage.plan.name}
                </span>
              </div>
              <p className="text-gray-400 mt-1">
                {usage.plan.dailyLimit.toLocaleString()} calls/day • {usage.plan.monthlyLimit.toLocaleString()} calls/month
              </p>
            </div>
          </div>
          <button className="flex items-center gap-2 px-4 py-2 bg-lime-400 hover:bg-lime-300 text-gray-900 font-medium rounded-lg transition-colors">
            Upgrade Plan
            <ArrowUpRight className="w-4 h-4" />
          </button>
        </div>
      </div>

      {/* Usage Cards Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mb-8">
        {/* Today's Usage */}
        <div className="p-6 bg-gray-900 rounded-xl border border-gray-800">
          <div className="flex items-center gap-3 mb-4">
            <div className="w-10 h-10 rounded-lg bg-blue-500/10 flex items-center justify-center">
              <Activity className="w-5 h-5 text-blue-400" />
            </div>
            <div>
              <h3 className="text-lg font-semibold text-white">Today</h3>
              <p className="text-sm text-gray-500">Daily API calls</p>
            </div>
          </div>

          <div className="mb-4">
            <div className="flex items-end justify-between mb-2">
              <span className="text-3xl font-bold text-white">
                {usage.today.used.toLocaleString()}
              </span>
              <span className="text-gray-400">
                / {usage.plan.dailyLimit.toLocaleString()}
              </span>
            </div>
            <div className="w-full h-3 bg-gray-800 rounded-full overflow-hidden">
              <div
                className={`h-full transition-all duration-500 ${getProgressColor(dailyPercentage)}`}
                style={{ width: `${Math.min(dailyPercentage, 100)}%` }}
              />
            </div>
          </div>

          <div className="flex items-center justify-between text-sm">
            <span className="text-gray-400">Remaining</span>
            <span className={`font-medium ${usage.today.remaining === 0 ? 'text-red-400' : 'text-lime-400'}`}>
              {usage.today.remaining.toLocaleString()} calls
            </span>
          </div>
        </div>

        {/* This Month's Usage */}
        <div className="p-6 bg-gray-900 rounded-xl border border-gray-800">
          <div className="flex items-center gap-3 mb-4">
            <div className="w-10 h-10 rounded-lg bg-purple-500/10 flex items-center justify-center">
              <Calendar className="w-5 h-5 text-purple-400" />
            </div>
            <div>
              <h3 className="text-lg font-semibold text-white">This Month</h3>
              <p className="text-sm text-gray-500">Monthly API calls</p>
            </div>
          </div>

          <div className="mb-4">
            <div className="flex items-end justify-between mb-2">
              <span className="text-3xl font-bold text-white">
                {usage.month.used.toLocaleString()}
              </span>
              <span className="text-gray-400">
                / {usage.plan.monthlyLimit.toLocaleString()}
              </span>
            </div>
            <div className="w-full h-3 bg-gray-800 rounded-full overflow-hidden">
              <div
                className={`h-full transition-all duration-500 ${getProgressColor(monthlyPercentage)}`}
                style={{ width: `${Math.min(monthlyPercentage, 100)}%` }}
              />
            </div>
          </div>

          <div className="flex items-center justify-between text-sm">
            <span className="text-gray-400">Remaining</span>
            <span className={`font-medium ${usage.month.remaining === 0 ? 'text-red-400' : 'text-lime-400'}`}>
              {usage.month.remaining.toLocaleString()} calls
            </span>
          </div>
        </div>
      </div>

      {/* Free Tier Notice */}
      {usage.plan.name.toLowerCase() === 'free' && (
        <div className="p-4 bg-blue-900/20 border border-blue-800/50 rounded-lg">
          <div className="flex items-start gap-3">
            <TrendingUp className="w-5 h-5 text-blue-400 mt-0.5" />
            <div>
              <p className="text-blue-300 font-medium">Free Tier Limits</p>
              <p className="text-blue-400/80 text-sm mt-1">
                Your free tier includes {usage.plan.dailyLimit.toLocaleString()} API calls per day 
                and {usage.plan.monthlyLimit.toLocaleString()} calls per month. 
                Upgrade to a paid plan for higher limits and priority support.
              </p>
              <button className="mt-3 text-sm text-blue-400 hover:text-blue-300 font-medium transition-colors">
                View upgrade options →
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Quota Warning */}
      {(dailyPercentage >= 80 || monthlyPercentage >= 80) && (
        <div className="mt-6 p-4 bg-amber-900/20 border border-amber-800/50 rounded-lg">
          <div className="flex items-start gap-3">
            <AlertCircle className="w-5 h-5 text-amber-400 mt-0.5" />
            <div>
              <p className="text-amber-300 font-medium">Approaching Quota Limit</p>
              <p className="text-amber-400/80 text-sm mt-1">
                {dailyPercentage >= 80 && 'You have used over 80% of your daily quota. '}
                {monthlyPercentage >= 80 && 'You have used over 80% of your monthly quota. '}
                Consider upgrading your plan to avoid service interruptions.
              </p>
            </div>
          </div>
        </div>
      )}

      {/* Top Endpoints by Calls */}
      {usage.topEndpoints && usage.topEndpoints.length > 0 && (
        <div className="mt-8">
          <h3 className="text-lg font-semibold mb-4">Top Endpoints (This Month)</h3>
          <div className="bg-gray-900 rounded-lg border border-gray-800 overflow-hidden">
            <table className="w-full">
              <thead>
                <tr className="border-b border-gray-800">
                  <th className="text-left px-4 py-3 text-sm font-medium text-gray-400">Endpoint</th>
                  <th className="text-right px-4 py-3 text-sm font-medium text-gray-400">Calls</th>
                </tr>
              </thead>
              <tbody>
                {usage.topEndpoints.map((ep, i) => (
                  <tr key={ep.endpointId} className={i !== usage.topEndpoints!.length - 1 ? 'border-b border-gray-800/50' : ''}>
                    <td className="px-4 py-3">
                      <span className="text-white">{ep.name}</span>
                      <span className="text-xs text-gray-500 ml-2">{ep.endpointId}</span>
                    </td>
                    <td className="px-4 py-3 text-right font-mono text-lime-400">
                      {ep.calls.toLocaleString()}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      )}

      {/* Quick Stats */}
      <div className="mt-8 grid grid-cols-2 md:grid-cols-4 gap-4">
        <div className="p-4 bg-gray-900/50 rounded-lg border border-gray-800/50">
          <p className="text-sm text-gray-500">Daily Usage</p>
          <p className="text-2xl font-bold text-white">{dailyPercentage.toFixed(1)}%</p>
        </div>
        <div className="p-4 bg-gray-900/50 rounded-lg border border-gray-800/50">
          <p className="text-sm text-gray-500">Monthly Usage</p>
          <p className="text-2xl font-bold text-white">{monthlyPercentage.toFixed(1)}%</p>
        </div>
        <div className="p-4 bg-gray-900/50 rounded-lg border border-gray-800/50">
          <p className="text-sm text-gray-500">Account Status</p>
          <p className="text-lg font-medium text-lime-400 capitalize">{usage.account?.status || 'active'}</p>
        </div>
        <div className="p-4 bg-gray-900/50 rounded-lg border border-gray-800/50">
          <p className="text-sm text-gray-500">Plan Type</p>
          <p className="text-lg font-medium text-white capitalize">{usage.plan.name}</p>
        </div>
      </div>
    </div>
  )
}

